import "../hero/hero.scss";
import React, { useState } from 'react';
import { motion } from "framer-motion";

const Hero = () => {

    const [isPopoverVisible, setPopoverVisible] = useState(false);

    const togglePopover = () => {
        setPopoverVisible(!isPopoverVisible);
    };

    const fadeInOut = {
        hidden: { opacity: 0, y: 50 },
        visible: { opacity: 1, y: 0, transition: { duration: 0.8, type: 'spring', stiffness: 300 } },
    };

  return (
    <div className='main-video'>
                <h1 className='title-main'>
                    PSICOTERAPIA PARA TU SALUD MENTAL</h1>
                <div className="video-container">
                    <div className="video">
                        <iframe
                            height="315"
                            src="https://www.youtube.com/embed/KsThEXZS9wI?si=c3otqOWsIYGhzIBf"
                            title="YouTube video player"
                            frameborder="0"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                            referrerpolicy="strict-origin-when-cross-origin"
                            allowfullscreen
                        ></iframe>
                    </div>
                    <div className="pop-message">
                        <button className='pop' onClick={togglePopover}>
                            Emociones
                        </button>
                        {isPopoverVisible && (
                            <motion.div
                                className='popover'
                                initial="hidden"
                                animate={isPopoverVisible ? "visible" : "hidden"}
                                variants={fadeInOut}
                            >
                                Gestionar bien las emociones es crucial porque impacta en la comunicacin, las relaciones y la resiliencia. Esto nos permite enfrentar desafos con equilibrio, tomar decisiones racionales y evitar impulsividad. Adems, mejora el trabajo en equipo y el bienestar mental, reduciendo el riesgo de ansiedad y estrs.
                            </motion.div>
                        )}
                    </div>
                    <div className="maintext-video">
                        <h2>Consulta psicolgica presencial, virtual y domiciliaria</h2>
                        <p className='text-video'>
                        Mi psicoterapia se centra en el desarrollo de tu Salud Mental, desarrollando tu potencial personal, mejorando tu relacin contigo mismo y tu autoconocimiento, gestionando y regulando tus emociones de forma distinta, comprendiendo tus vnculos afectivos pasados y actuales, construyendo nuevos sentidos y significados de vida, adems, de avanzar hacia diferentes formas de relacionamiento de pareja y familiares, ms estables y duraderas.
                        </p>
                    </div>
                </div>
            </div>
  )
}

export default Hero